}

.custom {
  display: block;
  position: relative;
  padding-left: 23px;
  padding-top: 7px;
  padding-bottom: 7px;
  cursor: pointer;
  font-size: 14px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.custom input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  margin: 0;
}

.checkmark {
  position: absolute;
  top: 7px;
  left: 0;
  height: 18px;
  width: 18px;
  background-color: none;
  border-radius: 50%;
  border: 2px solid #757575;
}

.checkBox .checkmark {
  border-radius: 0;
  width: 22px;
  height: 22px;
  top: -2px;
}

.custom:hover .checkmark {
  background-color: #fff;
}

.custom input:checked ~ .checkmark {
  background-color: #fff;
  border-color: #51b5b1;
}

.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

.custom input:checked ~ .checkmark:after {
  display: block;
}

.custom .checkmark:after {
  top: 2px;
  left: 2px;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: #51b5b1;
}

.checkBox .checkmark:after {
  left: 1px;
  top: -3px;
  content: "\f00c";
  font-family: "fontawesome";
  position: absolute;
  font-size: 16px;
  color: #51b5b1;
  background: none;
  width: 0;
  height: 0;
}

