  color: white;
}
.MuiOutlinedInput-notchedOutline {
  border-color: white !important;
}

.blue-btn:hover {
  font-size: 20px;
  transition: all 0.3s ease-out 0s;
}

.red-btn:hover {
  font-size: 17px;
  transition: all 0.3s ease-out 0s;
}
.blue-btn-footer:hover {
  font-size: 17px;
  transition: all 0.3s ease-out 0s;
}

.add_product_btn:hover {
  font-size: 22px;
  transition: all 0.3s ease-out 0s;
}
.w-100 {
  width: 100%;
}

.h-300 {
  height: 300px;
}

.contain-fit {
  object-fit: contain;
}

.w-200 {
