  font-family: "Besley", serif;
  font-size: 20px;
}

.modal {
  position: fixed;

  z-index: 100;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
}
.login-btn:hover {
  font-size: 20px;
}
.modal-container {
  border-radius: 10px;
  width: 500px;
  padding: 10px;
  background-color: #fff;
}
.modal-header {
  font-size: 20px;
}
.modal-header .modal-footer {
  padding: 10px;
}
.modal-body {
  display: flex;
  align-items: center;
  padding: 10px;
  border-top: 1px solid #eee;
  border-bottom: 1px solid #eee;
}
.rating-radio-btn {
  display: flex;
}
.rating-btn {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 5px;
}
.modal-body2 {
  border-top: 1px solid #eee;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-bottom: 1px solid #eee;
}

.modal-footer {
  padding: 10px;
  display: flex;
  justify-content: space-around;
}

