@media only screen and (max-width: 1024px) {
  .toolrow {
    padding-bottom: 5px;
  }
  .product-col {
    float: none;
    width: auto;
    flex: 0 0 100%;
    max-width: 100%;
  }

  .product-col {
    margin-left: 0;
    margin-right: 0;
  }

  .show-fav .products-list,
  .show-fav .fav-list {
    float: none;
    width: auto;
    padding-left: 5px;
    padding-right: 5px;
  }

  .products-list {
    padding: 15px;
  }

  .toolbar {
    float: none;
    clear: both;
    justify-content: center;
  }

  .toolrow {
    border-bottom: 0;
  }

  .toolrow li {
    padding-left: 10px;
    text-align: center;
    border: 0px;
    min-height: 57px;
  }

  .toolrow li:first-child {
    padding-left: 10px;
  }

  .toolrow li:last-child {
    border-right: 1px solid #e6e6e6;
  }

  .toolrow li:nth-child(3) {
    border-right: 0;
  }

  .toolrow li.show-ratings {
    padding-top: 15px;
  }

  .box .product-img span {
    border: 1px solid #f8f8f8;
    box-shadow: rgba(0, 0, 0, 0.05) 0 0 10px;
  }

  .box .product-img img {
    object-fit: contain;
  }

  .show-fav .fav-list {
    order: 1;
    max-width: 100%;
    width: 100%;
  }

  .show-fav .products-list {
    order: 2;
  }
}

@media only screen and (max-width: 990px) {
  .toolrow li {
    flex: 0 0 50%;
    text-align: left;
  }

  .toolrow li:nth-child(3) {
    border-right: 1px solid #e6e6e6;
  }

  .toolrow li:nth-child(5) {
    max-width: 100%;
    flex: 0 0 100%;
  }

  .toolbar .toolrow:last-child li:last-child {
    border-right: 0;
    border-bottom: 0;
  }

  .toolrow li:nth-child(2),
  .toolrow li:nth-child(4),
  .toolrow li:last-child {
    border-right: 0;
  }

  /*.toolbar .toolrow:last-child li:first-child{border-bottom:0;}*/
}

@media only screen and (max-width: 800px) {
  .toolrow {
    padding-bottom: 5px;
  }
  .toolrow li {
    padding-left: 10px;
    text-align: center;
    border: 0px;
  }

  .mar-left {
    margin-left: -30px;
  }

  .links,
  .video-sec,
  .foter-logo {
    max-width: 100%;
    flex: 0 0 100%;
    max-width: 100%;
    padding: 20px 0;
  }

  .foter-logo img {
    width: auto;
  }

  .footer-p {
    padding: 0 20px;
  }

  .fav-card {
    width: 100%;
  }

  .fav-img {
    display: inline-block;
    width: 65px;
    height: 65px;
    overflow: hidden;
    border-radius: 50%;
    vertical-align: middle;
  }
}

@media only screen and (max-width: 700px) {
  .toolrow {
    padding-bottom: 5px;
  }
  .show-fav .fav-list {
    padding-left: 15px;
    padding-right: 15px;
    padding-top: 15px;
    padding-bottom: 15px;

    margin-left: 0;
    margin-right: 0;
  }

  .products-list {
    padding: 15px;
  }

  .fav-clm {
    width: 30%;
  }

  .navbar-toggle {
    top: -25px;
  }

  /*.collapse:not(.show){display:inherit;}*/
  .collapse.in {
    display: block;
  }
}

@media only screen and (max-width: 600px) {
  .social .company {
    padding-bottom: 15px;
  }

  .fav-clm {
    width: 30%;
  }

  .favbox .fav-clm:last-child {
    width: 30%;
  }

  .favbox .fav-clm:first-child {
    width: 70%;
  }

  .favbox h4 {
    font-size: 16px;
  }
  /*
  .toolrow {
    padding: 0 15px;
  }
*/
  .main-footer {
    padding: 0 20px;
  }

  header {
    padding-left: 15px;
    padding-right: 15px;
  }

  header .logo {
    max-width: 60%;
  }

  .navbar-toggle {
    top: -35px;
  }
}

@media only screen and (max-width: 560px) {
  .fav-clm {
    width: 30%;
    text-align: center;
  }

  .favbox .fav-clm:last-child {
    width: 30%;
  }

  .favbox .fav-clm:first-child {
    width: 70%;
  }

  .toolrow li strong {
    display: block;
    padding-bottom: 5px;
  }

  .toolrow li:nth-child(5) strong {
    display: inline-block;
    padding-bottom: 0;
  }
}

@media only screen and (max-width: 500px) {
  .toolrow {
    padding-bottom: 5px;
  }
  .rating-btn {
    padding: 3px;
  }
  .modal-container {
    width: 280px;
  }
  .container-footer {
    padding: 0;
  }
  .donations {
    font-size: 10px;
  }
  .bubbles {
    flex-direction: column;
  }
  .bubbles img {
    max-width: 20%;
  }
  .bubbles a {
    font-size: 12px;
  }
  .agradecimientos {
    font-size: 12px;
  }
  .social-network {
    font-size: 10px;
  }

  .header-title {
    width: 130px;
    word-wrap: break-word;
    text-align: center;
  }
  .header-title a {
    font-size: 15px;
  }
  .header-title img {
    width: 90px;
    height: 40px;
  }
  .text-dark {
    font-size: 15px;
  }
  .social .company {
    padding-bottom: 15px;
  }
  .search-bar {
    width: 100%;
    padding: 10px 10px 10px 10px;
  }
  .search-bar .d-flex {
    width: 100px;
  }
  .search-bar input[type="text"] {
    width: 100px;
  }
  .show-products {
    width: 100%;
  }

  .dropdown_sort {
    display: flex;
    flex-direction: column;

    align-items: center;
  }

  .box .product-img,
  .box .product-info {
    flex: 0 0 100%;
    max-width: 100%;
  }

  .box .product-info {
    padding: 20px 0 0;
  }

  /* .product-info p {
        min-height: inherit;
        max-height: inherit;
      } */
}

@media only screen and (max-width: 390px) {
  .container-footer {
    padding: 0;
  }
  .donations {
    font-size: 10px;
  }
  .bubbles {
    flex-direction: column;
  }
  .bubbles img {
    max-width: 20%;
  }
  .bubbles a {
    font-size: 12px;
  }
  .agradecimientos {
    font-size: 12px;
  }
  .social-network {
    font-size: 10px;
  }
  .container-footer a {
    margin: 5px;
  }
  .action span {
    flex: 0 0 100%;
    text-align: left;
    padding: 10px 0;
  }

  .fav-clm {
    width: 30%;
  }

  .favbox .fav-clm:last-child {
    width: 30%;
  }

  .favbox .fav-clm:first-child {
    width: 70%;
  }

  .toolrow li big {
    font-size: 14px;
    line-height: 18px;
    padding-bottom: 5px;
    display: block;
    padding-right: 0px;
  }

  .toolrow li label {
    margin-right: 30px;
  }

  .toolrow li {
    padding-right: 0;
  }

  .navbar-toggle {
    top: -25px;
  }
}

@media only screen and (max-width: 340px) {
  .toolrow li label {
    margin-right: 15px;
  }
}

@media only screen and (max-width: 768px) {
  .mobile-container .text-lg-left {
    display: none;
  }
}
